import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService} from 'angular2-flash-messages';
import { Router } from '@angular/router';


@Component({
  moduleId: module.id,
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
	email: String;
	password: String;

  constructor(
		private validateService: ValidateService,
		private flashMessage: FlashMessagesService,
		private authService: AuthService,
		private router: Router
	) { }

  ngOnInit() {
  }
	onLoginSubmit(){
		// console.log(this.email);
		const user = {
			email: this.email,
			password: this.password
		};
		// do validation for email
		if (!this.validateService.validateEmail(user.email)){
			this.flashMessage.show("Not a valid email!", {cssClass: 'alert-danger', timeout: 2000});
			return false;
		};
		// this returns observable so we subscribe to it
		this.authService.authenticateUser(user).subscribe(data => {
			console.log(data);
			if (data.success){
				this.authService.storeUserData(data.user, data.token);
				this.flashMessage.show(data.msg, {cssClass: 'alert-success', timeout: 2000});
        console.log(' you are redirecting to profile!');
				this.router.navigate(['/dashboard']);
			}else{ 			
				this.flashMessage.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
				this.router.navigate(['/login']);
			}
		});
	}
}



